var app = angular.module('ForecastApp', []);
/*translate temperature from kelvin to cels*/
app.filter('Cel', function () {
    return function (temp) {
        return Math.ceil(parseFloat(temp) - 273.15);
    };
});
/**
 * Created by h3rme on 30.09.2016.
 */
app.controller('ForecastController',['$scope','forecast', function ($scope, forecast) {
    $scope.text='';
    $scope.weather;
    $scope.view = false;
    $scope.submit = function () {
        if($scope.text){
            forecast.getData(this.text).success(function (data) {
                $scope.weather = data;
                console.log($scope.weather);
            });
            $scope.view = true;
            $scope.text = '';
        }
    }
}]);

/*API that i used: http://api.openweathermap.org/data/2.5/weather?q=London,uk&appid=03d17efcc1443561cd0b9ad3b5effe36*/
/**
 * Created by h3rme on 21.10.2016.
 */
app.factory('forecast', ['$http', function($http) {
    return {
        getData: function (city) {
           return  $http.get('http://api.openweathermap.org/data/2.5/weather?q='+city+'&appid=03d17efcc1443561cd0b9ad3b5effe36')
               .success(function (data) {
                   return data;
               })
               .error(function (err) {
                   return err;
               });
        }
    }
}]);
